@charset "utf-8";
@import url('https://fonts.googleapis.com/css2?family=DynaPuff&display=swap');

/* CSS Document */

html {
  margin: 0 !important;
  padding: 0;
  height: 100%;
  overflow: hidden;
  position: relative;
}

body{
    display: flex;
    min-height: 100%;
    height: 100%;
    overflow: auto;
    position: relative;
    margin-top: 0px; 
    margin-bottom: 0px; 
    margin-left: 0px; 
    margin-right: 0px;
    padding: 0;
    color: black; 
    font-size: 16px; 
    font-family: -apple-system, BlinkMacSystemFont, "DynaPuff", system-ui;
    font-weight: 400;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #E2E2E2;
}

h1,h2,h3,h4,p, li {
  margin: 0;
  color: #134984;
}

h3 {
  font-style: italic;
}

#root {
  display: block;
  position: relative;
  height: 100%;
  max-width: 600px;
  min-width: 280px;
  margin: 0 auto;
}